import React from 'react';

import styled from 'styled-components';
import mq from '../../components/MediaQuery';

import parallax2 from '../../assets/img/parallax2.jpg';

import p1 from '../../assets/img/p1.jpg';
import p2 from '../../assets/img/p2.jpg';
import p3 from '../../assets/img/p3.jpg';
import p4 from '../../assets/img/p4.jpg';
import p5 from '../../assets/img/p5.jpg';
import p6 from '../../assets/img/p6.jpg';
import p7 from '../../assets/img/p7.jpg';
import p8 from '../../assets/img/p8.jpg';

const PortfolioContainer = styled.div`
    //background-color: #f0f3;

    .content {
        max-width: 1200px;
        margin: auto;
        padding: 60px 20px;

        ${mq.maxWidth("md")`
            padding: 30px 10px;
        `}

        h1 {
            text-align: center;
            font-size: 40px;
            margin-bottom: 32px;
            color: #424242;

            ${mq.maxWidth("md")`
                font-size: 30px;
                margin-bottom: 24px;
            `}
        }

        p {
            text-align: center;
            margin-bottom: 24px;
            font-size: 20px;
            color: #616161;
            font-weight: 300;
            font-style: italic;

            ${mq.maxWidth("md")`
                font-size: 16px;
                margin-bottom: 20px;
            `}
        }

        .gallery {
            display: flex;
            flex-wrap: wrap;
            margin-top: 50px;

            li {
                width: 25%;
                padding: 10px;

                ${mq.maxWidth("md")`
                    width: 50%;
                `}

                a {
                    display: block;
                    overflow: hidden;

                    img {
                        width: 100%;
                        object-fit: cover;
                        transition: all 0.3s ease-in-out;

                        &:hover {
                            transform: scale(1.2, 1.2);
                        }
                    }
                }
            }
        }
    }
`;

const Portfolio = () => {
    const pimgs = [p1, p2, p3, p4, p5, p6, p7, p8];

    return (
        <PortfolioContainer>
            <div className="title" style={{backgroundImage: `url(${parallax2})`}}>
                <div className="hello">
                    <h1>portfolio</h1>
                    <p>This is my work</p>
                </div>
            </div>
            <div className="content">
                <h1>MY WORK</h1>
                <p>Here are some of my latest lorem work ipsum tipsum.</p>
                <p>Click on the images to make them bigger</p>
                <ul className="gallery">
                    {pimgs.map((v, i) => {
                        return (
                            <li key={i}>
                                <a href="#">
                                    <img src={v} />
                                </a>
                            </li>
                        );
                    })}
                </ul>
            </div>
        </PortfolioContainer>
    );
};

export default Portfolio;