/**   */
import { createGlobalStyle } from "styled-components";
import reset from 'styled-reset';
import mq from './MediaQuery';

/**      */
const GlobalStyles = createGlobalStyle`
    ${reset}

    body {
        * {
            font-family: "Noto Sans KR";
            box-sizing: border-box;
        }

        a {
            text-decoration: none;
            color: #000;
        }

        .container {
            max-width: 1200px;
            margin: auto;
        }
    }

    /* index     */
    .title {
        width: 100%;
        // vh viewport height   %
        height: 40vh;
        display: flex;
        background-size: cover;
        background-position: center center;
        background-attachment: fixed;

        .hello {
            // display: flex    margin: auto  
            margin: auto;

            color: #f1f1f1;
            text-align: center;
            text-transform: uppercase;
            padding: 30px 50px;

            ${mq.maxWidth('md')`
                padding: 20px 30px;
            `}

            h1 {
                font-size: 36px;
                font-weight: 600;
                white-space: nowrap;
                margin-bottom: 10px;

                ${mq.maxWidth('md')`
                    font-size: 26px;
                `}
            }

            p {
                font-size: 20px;
                font-weight: 300;

                ${mq.maxWidth('md')`
                    font-size: 16px;
                `}
            }
        }
    }
`;

export default GlobalStyles;