import React from 'react';

import styled from 'styled-components';
import mq from '../../components/MediaQuery';

import parallax3 from '../../assets/img/parallax3.jpg';

const ContactContainer = styled.div`
    //background-color: #06f3;

    .content {
        max-width: 1200px;
        margin: 30px auto;
        display: flex;
        flex-wrap: wrap;

        .map {
            width: 50%;
            height: 40vh;
            padding: 20px;

            ${mq.maxWidth("md")`
                width: 100%;
            `}
        }

        .contact {
            width: 50%;
            padding: 20px;

            ${mq.maxWidth("md")`
                width: 100%;
            `}

            ul {
                display: flex;
                flex-direction: column;
                height: 100%;
                justify-content: space-evenly;

                li {
                    font-size: 26px;
                    color: #616161;
                    padding-left: 25px;
                    line-height: 150%;

                    i {
                        margin-right: 10px;
                    }
                }
            }
        }
    }
`;

const Contact = () => {
    return (
        <ContactContainer>
            <div className="title" style={{backgroundImage: `url(${parallax3})`}}>
                <div className="hello">
                    <h1>contact</h1>
                    <p>Address, Email, Phone</p>
                </div>
            </div>
            <div className="content">
                <div className="map">
                    <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d791.3298152142662!2d127.03093062331529!3d37.50038759454418!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x357ca1652c6e31a7%3A0x7fe683e301da6726!2z66mU6rCA7Iqk7YSw65SUSVTslYTsubTrjbDrr7g!5e0!3m2!1sko!2skr!4v1700133598643!5m2!1sko!2skr" width="100%"
                    height="100%"
                    style={{border: '0px'}}
                    allowFullScreen=""
                    loading="lazy"
                    referrerPolicy="no-referrer-when-downgrade"></iframe>
                </div>
                <div className="contact">
                    <ul>
                        <li>
                            <i className="fa-solid fa-location-dot"></i>
                              94 
                        </li>
                        <li>
                            <i className="fa-solid fa-phone"></i>
                            <a href="tel:02-588-9991">Phone: 02-588-9991</a>
                        </li>
                        <li>
                            <i className="fa-solid fa-envelope"></i>
                            <a href="mailto:mail@mail.com">Email: mail@mail.com</a>
                        </li>
                    </ul>
                </div>
            </div>
        </ContactContainer>
    );
};

export default Contact;